import React from 'react'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Tooltip from '@mui/material/Tooltip'
import { styled } from '@mui/material/styles'
import GitHubIcon from '@mui/icons-material/GitHub'
import MenuBookIcon from '@mui/icons-material/MenuBook'

import { useRunPipeline } from 'store/slice/Pipeline/PipelineHook'
import FlowChart from './FlowChart/FlowChart'
import Visualize from './Visualize/Visualize'
import Experiment from './Experiment/Experiment'
import { Box } from '@mui/material'
import { useSearchParams } from 'react-router-dom'
// import studioLogo from './studio.png'

const Project: React.FC = () => {
  const [searchParams] = useSearchParams()
  const [value, setValue] = React.useState(Number(searchParams.get('tab') || 0))
  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue)
  }

  const runPipeline = useRunPipeline() // タブ切り替えによって結果取得処理が止まってしまうのを回避するため、タブの親レイヤーで呼び出している

  return (
    <RootDiv>
      <StyledAppBar color="inherit">
        <Toolbar variant="dense">
          {/* <img src={studioLogo} alt="studio" width={75} height={50} /> */}
          <Tabs
            sx={{ width: '100%' }}
            value={value}
            onChange={handleChange}
            centered
            textColor="primary"
          >
            <Tab label="Workflow" {...a11yProps(0)} />
            <Tab label="Results" {...a11yProps(1)} />
            <Tab label="Visualize" {...a11yProps(2)} />

          </Tabs>
          <Tooltip title="GitHub repository">
            <IconButton
              sx={{ mr: 1 }}
              href="https://github.com/oist/studio"
              target="_blank"
            >
              <GitHubIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Documentation">
            <IconButton
              href="https://studio.readthedocs.io/en/latest/"
              target="_blank"
            >
              <MenuBookIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
      </StyledAppBar>
      <TabPanel value={value} index={0}>
        <FlowChart {...runPipeline} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Visualize />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Experiment />
      </TabPanel>
    </RootDiv>
  )
}

const RootDiv = styled('div')(({ theme }) => ({
  flexGrow: 1,
  backgroundColor: theme.palette.background.paper,
  height: '100%',
}))

const StyledAppBar = styled(Box)(({ theme }) => ({
  zIndex: theme.zIndex.drawer + 1,
  backgroundColor: '#E1DEDB',
}))

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      style={{ height: 'calc(100% - 58px)' }}
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ height: '100%' }}>{children}</Box>}
    </div>
  )
}

function a11yProps(index: number | string) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}

export default Project
